import React, { useEffect, useState } from 'react';
import { useLiff } from 'react-liff';
import {Container, Navbar, Nav} from "react-bootstrap";
import {Button, Col, Row, Card} from "react-bootstrap";
import {Form, Modal} from "react-bootstrap";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faList } from '@fortawesome/free-solid-svg-icons';
import { faPlus } from '@fortawesome/free-solid-svg-icons';
import { faBell } from '@fortawesome/free-solid-svg-icons';
import { db } from '../components/firebase';
import { collection, addDoc, getDocs, getDoc, deleteDoc, doc, setDoc } from "firebase/firestore";
import Loader from '../components/Loader';
import resources from '../components/resources';
import './Home.css';

const Home = () => {
    const [notReady, setNotReady] = useState(true);
    const [validated, setValidated] = useState(false);
    const [showFunc, setShowFunc] = useState({
        List: true,
        Add: false,
        Subscription: false
    });
    const [profile, setProfile] = useState({
        "userId": "",
        "displayName": "",
        "pictureUrl": "",
        "statusMessage": ""
    });
    const { error, liff, isLoggedIn, ready } = useLiff();
    useEffect(() => {
        if (!isLoggedIn) return;
        (async () => {
            const profile = await liff.getProfile();
            setProfile({
                "userId": profile.userId,
                "displayName": profile.displayName,
                "pictureUrl": profile.pictureUrl,
                "statusMessage": profile.statusMessage,
            });
            (async () => {
                const docRef = doc(db, "Remind2", profile.userId);
                const docSnap = await getDoc(docRef);
                if (docSnap.exists()) {
                    console.log("Document data:", docSnap.data());
                    setProfile({...profile, notify: docSnap.data().notify});
                } else {
                    // doc.data() will be undefined in this case
                    console.log("No such document!");
                    return window.location.href = "https://notify-bot.line.me/oauth/authorize?" +
                        "response_type=code&" +
                        "client_id=MiC6LIxtzUOk6sipXu6XEN&" +
                        "redirect_uri=https://90418139.github.io/React/notify&" +
                        "scope=notify&" +
                        "state=" + profile.userId;
                }
            })();
        })();
    }, [liff, isLoggedIn]);

    function Subscription() {
        const [data, setData] = useState({
            notify: profile.notify,
            PCR_L: false,
            BFI82U_L: false,
        });
        useEffect(() => {
            (async () => {
                const docRef = doc(db, "Subscribe", profile.userId);
                const docSnap = await getDoc(docRef);
                if (docSnap.exists()) {
                    console.log("Document data:", docSnap.data());
                    setData(docSnap.data());
                } else {
                    // doc.data() will be undefined in this case
                    console.log("No such document!");
                }
            })();
            setNotReady(false);
        },[profile]);
        const switchChange = (e) =>{
            setData({...data, [e.target.name]: !data[e.target.name]});
        };
        const handleChange = (e) =>{
            setData({ ...data, [e.target.name]: e.target.value });
        };
        const handleSubmit = (event) => {
            event.preventDefault();
            event.stopPropagation();
            (async () => {
                try {
                    await setDoc(doc(db, "Subscribe", profile.userId), data);
                    alert('Save Success!');
                } catch (e) {
                    console.error("Error adding document: ", e);
                    alert('Save Error!');
                }
                setValidated(false);
            })();
        };
        if (!ready | notReady) return <Loader/>;
        if (!isLoggedIn) return liff.login();
        return (
            <Row>
                <Col sm={12} lg={6} className="mb-2">
                    <Form onSubmit={handleSubmit}>
                        <Card className="shadow-sm">
                            <Card.Header as="h5" className="bg-secondary text-light">訂閱</Card.Header>
                            <Card.Body className="text-center">
                                <Card.Title>Put/Call Ratio</Card.Title>
                                <div className="form-check form-check-inline">
                                    <Form.Check type="switch" id="PCR_L" label="Line" name="PCR_L" checked={data.PCR_L}
                                                onChange={switchChange} />
                                    <Form.Text muted>
                                        Send at 15:20 PM every working day.
                                    </Form.Text>
                                </div>
                            </Card.Body>
                            <hr/>
                            <Card.Body className="text-center">
                                <Card.Title>三大法人買賣金額統計</Card.Title>
                                <div className="form-check form-check-inline">
                                    <Form.Check type="switch" id="BFI82U_L" label="Line" name="BFI82U_L" checked={data.BFI82U_L}
                                                onChange={switchChange} />
                                    <Form.Text muted>
                                        Send at 15:20 PM every working day.
                                    </Form.Text>
                                </div>
                            </Card.Body>
                            <hr/>
                            <Card.Body className="text-center">
                                <Card.Title>近一周類股漲跌股價前20名</Card.Title>
                                <Form.Group className="mb-3">
                                    <Form.Select name="CMONEY" onChange={handleChange} value={data.CMONEY}>
                                        {['不訂閱' ,'近一日', '近一周', '近一月', '近一季', '近一年', '今年以來'].map((value, index)=>(
                                            <option key={index} value={index}>{value}</option>
                                        ))}
                                    </Form.Select>
                                    <Form.Text muted>
                                        Send at 15:20 PM every working day.
                                    </Form.Text>
                                </Form.Group>
                            </Card.Body>
                            <Card.Footer>
                                <Col className="d-grid gap-2">
                                    <Button type="submit" variant="primary" className="rounded-pill">Save changes</Button>
                                </Col>
                            </Card.Footer>
                        </Card>
                    </Form>
                </Col>
            </Row>
        );
    }

    function AddRemind() {
        const [data, setData] = useState({
            userId: profile.userId
        });
        const handleChange = (e) =>{
            setData({ ...data, [e.target.name]: e.target.value });
        };
        const handleSubmit = (event) => {
            const form = event.currentTarget;
            event.preventDefault();
            event.stopPropagation();
            setValidated(true);
            if (form.checkValidity()) {
                (async () => {
                    try {
                        const docRef = await addDoc(collection(db, "Remind"), data);
                        console.log("Document written with ID: ", docRef.id);
                        alert('Create Success!');
                    } catch (e) {
                        console.error("Error adding document: ", e);
                        alert('Create Error!');
                    }
                    setValidated(false);
                })();
            }
        };
        if (!ready | notReady) return <Loader/>;
        if (!isLoggedIn) return liff.login();
        return (
            <Row>
                <Col sm={12} className="mb-2">
                    <Form noValidate validated={validated} onSubmit={handleSubmit}>
                        <Card className="shadow-sm">
                            <Card.Header as="h5" className="bg-secondary text-light">新增</Card.Header>
                            <Card.Body>
                                <Form.Group className="mb-3">
                                    <Form.Label>項目</Form.Label>
                                    <Form.Select name="type" onChange={handleChange} required>
                                        <option value="">未選取</option>
                                        { Object.keys(resources.type).map(key =>
                                            <option value={key}>{resources.type[key]} {key}</option>
                                        ) }
                                    </Form.Select>
                                </Form.Group>
                                <Form.Group className="mb-3">
                                    <Form.Label>標的</Form.Label>
                                    <Form.Select name="title" onChange={handleChange} required>
                                        <option value="">未選取</option>
                                        { typeof data.type != "undefined" && Object.keys(resources[data.type].title).map(key =>
                                            <option value={key}>{resources[data.type].title[key]} {key}</option>
                                        ) }
                                    </Form.Select>
                                </Form.Group>
                                <Form.Group className="mb-3">
                                    <Form.Label>條件</Form.Label>
                                    <Form.Select name="condition" onChange={handleChange} required>
                                        <option value="">未選取</option>
                                        <option value="P">價格</option>
                                        <option value="KD">KD</option>
                                    </Form.Select>
                                </Form.Group>
                                <Form.Group className="mb-3">
                                    <Form.Label>高</Form.Label>
                                    <Form.Control type="number" step={0.1} name="high" onChange={handleChange} required />
                                </Form.Group>
                                <Form.Group className="mb-3">
                                    <Form.Label>低</Form.Label>
                                    <Form.Control type="number" step={0.1} name="low" onChange={handleChange} required />
                                </Form.Group>
                                <Form.Group className="mb-3">
                                    <Form.Label>開始時間</Form.Label>
                                    <Form.Select name="time_s" onChange={handleChange} required>
                                        <option value="">未選取</option>
                                        {[...Array(24)].map((x, i) =>
                                            <option value={i}>{i}</option>
                                        )}
                                    </Form.Select>
                                    <Form.Text muted>
                                        00-24
                                    </Form.Text>
                                </Form.Group>
                                <Form.Group className="mb-3">
                                    <Form.Label>結束時間</Form.Label>
                                    <Form.Select name="time_e" onChange={handleChange} required>
                                        <option value="">未選取</option>
                                        {[...Array(24)].map((x, i) =>
                                            <option value={i}>{i}</option>
                                        )}
                                    </Form.Select>
                                    <Form.Text muted>
                                        00-24
                                    </Form.Text>
                                </Form.Group>
                                <Form.Group className="mb-3">
                                    <Form.Label>週期</Form.Label>
                                    <Form.Select name="cycle" onChange={handleChange} required>
                                        <option value="">未選取</option>
                                        {[...Array(59)].map((x, i) =>
                                            <option value={i+1}>{i+1}</option>
                                        )}
                                    </Form.Select>
                                    <Form.Text muted>
                                        每 1-59 分鐘一次
                                    </Form.Text>
                                </Form.Group>
                            </Card.Body>
                            <Card.Footer>
                                <Row>
                                    <Col className="d-grid gap-2">
                                        <Button type="submit" variant="primary" className="rounded-pill">提交</Button>
                                    </Col>
                                </Row>
                            </Card.Footer>
                        </Card>
                    </Form>
                </Col>
            </Row>
        );
    }

    function RemindList() {
        const [remind, setRemind] = useState([]);
        const [showEditModal, setShowEditModal] = useState({
            show: false,
        });
        const handleChange = (e) =>{
            setShowEditModal({ ...showEditModal, [e.target.name]: e.target.value });
        };
        const handleSubmit = (event) => {
            const form = event.currentTarget;
            event.preventDefault();
            event.stopPropagation();
            setValidated(true);
            if (form.checkValidity()) {
                (async () => {
                    try {
                        const id = showEditModal.id;
                        delete showEditModal.show;
                        delete showEditModal.id;
                        await setDoc(doc(db, "Remind", id), showEditModal);
                        alert('Create Success!');
                    } catch (e) {
                        console.error("Error adding document: ", e);
                        alert('Create Error!');
                    }
                    //setValidated(false);
                })();
            }
        };
        const handleDelete = async (Id) => {
            if(window.confirm('確定刪除嗎？')){
                await deleteDoc(doc(db, "Remind", Id));
                const updatedData = remind.filter(function(item, index, array){
                    return item.id != Id;
                });
                setRemind(updatedData);
            }
        };
        useEffect(() => {
            (async () => {
                const querySnapshot = await getDocs(collection(db, "Remind"));
                const allRemind = querySnapshot.docs.map(doc => ({
                    id: doc.id,
                    ...doc.data()
                }));
                const filterAllRemind = allRemind.filter(function(item, index, array){
                    // return item.userId == 'U7c6536dd1a83a1b8a0267da1d5bf2c51';
                    return item.userId === profile.userId;
                });
                setRemind(filterAllRemind);
                setNotReady(false);
            })();
        }, []);
        if (!ready | notReady) return <Loader/>;
        if (!isLoggedIn) return liff.login();
        if (remind.length === 0) return <div>No any remind</div>;
        return(
            <Row>
                {remind.map((obj, i) =>(
                    <Col md={6} sm={12} className="mb-2" key={i}>
                        <Card className="bg-white border-white border-0 shadow-sm">
                            <div className="card-custom-img" />
                            <div className="card-custom-avatar">
                                <img src={'./images/' + obj.title + (obj.title == 'gold' ? '.png':'.svg')} alt={obj[4]} className="img-fluid"/>
                            </div>
                            <Card.Body>
                                <Row>
                                    <Col>項目</Col>
                                    <Col>
                                        {resources.type[obj.type]}
                                    </Col>
                                </Row>
                                <Row>
                                    <Col>條件</Col>
                                    <Col>
                                        {resources.condition[obj.condition]}
                                    </Col>
                                </Row>
                                <Row>
                                    <Col>標的</Col>
                                    <Col>
                                        {resources[obj.type].title[obj.title]}
                                    </Col>
                                </Row>
                                <Row>
                                    <Col>高</Col>
                                    <Col>{obj.high}</Col>
                                </Row>
                                <Row>
                                    <Col>低</Col>
                                    <Col>{obj.low}</Col>
                                </Row>
                                <Row>
                                    <Col>週期</Col>
                                    <Col>{obj.time_s} {obj.time_e} {obj.cycle}</Col>
                                </Row>
                            </Card.Body>
                            <Card.Footer style={{background: 'inherit', borderColor: 'inherit'}}>
                                <Row>
                                    <Col className="d-grid gap-2">
                                        <Button variant="warning" className="rounded-pill" onClick={() => setShowEditModal({show: true, ...obj})}>編輯</Button>
                                    </Col>
                                    <Col className="d-grid gap-2">
                                        <Button variant="danger" className="rounded-pill" onClick={() => handleDelete(obj.id)}>刪除</Button>
                                    </Col>
                                </Row>
                            </Card.Footer>
                        </Card>
                    </Col>
                ))}
                <Modal show={showEditModal.show} size="lg" onHide={() => setShowEditModal({show: false})}>
                    <Form noValidate validated={validated} onSubmit={handleSubmit}>
                        <Modal.Header closeButton>
                            <Modal.Title>Edit</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            <Form.Group className="mb-3">
                                <Form.Label>項目</Form.Label>
                                <Form.Select name="type" value={showEditModal.type} onChange={handleChange} required>
                                    <option value="">未選取</option>
                                    { Object.keys(resources.type).map(key =>
                                        <option value={key}>{resources.type[key]} {key}</option>
                                    ) }
                                </Form.Select>
                            </Form.Group>
                            <Form.Group className="mb-3">
                                <Form.Label>標的</Form.Label>
                                <Form.Select name="title" value={showEditModal.title} onChange={handleChange} required>
                                    <option value="">未選取</option>
                                    { typeof showEditModal.type != "undefined" && Object.keys(resources[showEditModal.type].title).map(key =>
                                        <option value={key}>{resources[showEditModal.type].title[key]} {key}</option>
                                    ) }
                                </Form.Select>
                            </Form.Group>
                            <Form.Group className="mb-3">
                                <Form.Label>條件</Form.Label>
                                <Form.Select name="condition" value={showEditModal.condition} onChange={handleChange} required>
                                    <option value="">未選取</option>
                                    <option value="P">價格</option>
                                    <option value="KD">KD</option>
                                </Form.Select>
                            </Form.Group>
                            <Form.Group className="mb-3">
                                <Form.Label>高</Form.Label>
                                <Form.Control type="number" step={0.1} name="high" value={showEditModal.high} onChange={handleChange} required />
                            </Form.Group>
                            <Form.Group className="mb-3">
                                <Form.Label>低</Form.Label>
                                <Form.Control type="number" step={0.1} name="low" value={showEditModal.low} onChange={handleChange} required />
                            </Form.Group>
                            <Form.Group className="mb-3">
                                <Form.Label>開始時間</Form.Label>
                                <Form.Select name="time_s" value={showEditModal.time_s} onChange={handleChange} required>
                                    <option value="">未選取</option>
                                    {[...Array(24)].map((x, i) =>
                                        <option value={i}>{i}</option>
                                    )}
                                </Form.Select>
                                <Form.Text muted>
                                    00-24
                                </Form.Text>
                            </Form.Group>
                            <Form.Group className="mb-3">
                                <Form.Label>結束時間</Form.Label>
                                <Form.Select name="time_e" value={showEditModal.time_e} onChange={handleChange} required>
                                    <option value="">未選取</option>
                                    {[...Array(24)].map((x, i) =>
                                        <option value={i}>{i}</option>
                                    )}
                                </Form.Select>
                                <Form.Text muted>
                                    00-24
                                </Form.Text>
                            </Form.Group>
                            <Form.Group className="mb-3">
                                <Form.Label>週期</Form.Label>
                                <Form.Select name="cycle" value={showEditModal.cycle} onChange={handleChange} required>
                                    <option value="">未選取</option>
                                    {[...Array(59)].map((x, i) =>
                                        <option value={i+1}>{i+1}</option>
                                    )}
                                </Form.Select>
                                <Form.Text muted>
                                    每 1-59 分鐘一次
                                </Form.Text>
                            </Form.Group>
                        </Modal.Body>
                        <Modal.Footer>
                            <Button variant="secondary" className="rounded-pill" onClick={() => setShowEditModal({show: false})}>Close</Button>
                            <Button type="submit" variant="primary" className="rounded-pill">Save changes</Button>
                        </Modal.Footer>
                    </Form>
                </Modal>
            </Row>
        );
    }

    return (
        <Container>
            <Row className="mb-5">
                <Col className="mb-5">
                    <h4>Welcome , {profile.displayName}!</h4>
                    {showFunc.List && <RemindList/>}
                    {showFunc.Add && <AddRemind/>}
                    {showFunc.Subscription && <Subscription/>}
                </Col>
            </Row>
            <Navbar fixed="bottom" bg="light">
                <Container fluid>
                    <Col sm={4} className="text-center">
                        <Nav.Link onClick={() => setShowFunc({
                            List: true,
                            Add: false,
                            Subscription: false
                        })}>
                            <FontAwesomeIcon icon={faList} /><br />
                            RemindList
                        </Nav.Link>
                    </Col>
                    <Col sm={4} className="text-center">
                        <Nav.Link onClick={() => setShowFunc({
                            List: false,
                            Add: true,
                            Subscription: false
                        })}>
                            <FontAwesomeIcon icon={faPlus} /><br />
                            AddRemind
                        </Nav.Link>
                    </Col>
                    <Col sm={4} className="text-center">
                        <Nav.Link onClick={() => setShowFunc({
                            List: false,
                            Add: false,
                            Subscription:true
                        })}>
                            <FontAwesomeIcon icon={faBell} /><br />
                            Subscription
                        </Nav.Link>
                    </Col>
                </Container>
            </Navbar>
        </Container>
    );
};

export default Home;
